.intro-container {
    margin: 0 auto;
    margin-top: 10vh;
    margin-right: 100px;
    margin-left: 100px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  
    a {
      font-weight: bold;
    }
  
    .intro-title {
      font-family: 'Monaco', Consolas, 'Lucida Console', monospace;
      font-size: 4.5em;
      color: rgba(72, 116, 197, 0.65);
      text-align: left;
  
      &:before {
        content: '>\a0';
      }
  
      span {
        background-color: rgba(72, 116, 197, 0.65);
        animation: caret 1s steps(1) infinite;
      }
  
      @keyframes caret {
        50% {
          background-color: transparent;
        }
      }
    }
  
    .intro-description {
      font-family: 'Camphor', Open Sans, Segoe UI, sans-serif;
      font-weight: bold;
      font-size: 1.4em;
      line-height: 40px;
      color: rgba(3, 7, 15, 0.65);
      margin: 80px 0;
      text-align: left;
    }
  }
